import vmTests from './modules/vmtests'

document.addEventListener("DOMContentLoaded", function() {

  var isIE = false;

  if(navigator.userAgent.indexOf('MSIE')!==-1 || navigator.appVersion.indexOf('Trident/') > 0)
    isIE = true

  if (!isIE){

    window.vmTests = function(){

      vmTests();

    }

    setTimeout(function(){
      if(!document.body.classList.contains('ie')){ // The below code does not work in IE11
        var liveURLs = ["www.virginmoneyukplc.com","uk.virginmoney.com","uk.virginmoneygiving.com"]
        if(!liveURLs.includes(window.location.host) || navigator.userAgent.indexOf("Chrome-Lighthouse") > -1){
          vmTests();
        }
      }
    }, 1000);

    // #region Apply some lintint CSS
    window.lint = function(){

      document.body.innerHTML += `
      <style type="text/css">
      html:not([lang]):after,  
      html[lang=""]:after {
        content: "No language tag";
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999999;
        background: red!important;
      } 
      
      meta[charset]:not([charset="UTF-8"]):after {
        content: "Incorrect character set defined";
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999999;
        background: red!important;
      } 
      
      meta[name="viewport"][content*="user-scalable=no"]:after,  
      meta[name="viewport"][content*="maximum-scale"]:after,  
      meta[name="viewport"][content*="minimum-scale"]:after {
        content: "Unaccessible viewport attributes";
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999999;
        background: red!important;
      }  
      
      a:not([href]),
      a[href="#"],  
      a[href=""],  
      a[href*="javascript:void(0)"],
      button:empty,  
      a:empty {
        content: "lint";
        outline: 3px dashed red!important;
        outline-offset: -3px!important;
      }  
      
      img:not([alt]) { 
        outline: 3px dashed red!important;
        outline-offset: -3px!important;
      }  
      
      input:not([id]),  
      select:not([id]),  
      textarea:not([id]) { 
        outline: 3px dashed red!important;
        outline-offset: -3px!important;
      }
      
      label:not([for]) {
        outline: 3px dashed red!important;
        outline-offset: -3px!important;
      }  
      </style>
      `;
      
      return "Lintint CSS added";
    }
    // #endregion
  }
});